export const formRules = {
    AccountRules: [
        v => !!v || '',
        v => (v && v.length <= 20) || '20',
    ],
    PasswordRules: [
        v => !!v || '',
        v => (v && v.length <= 20) || '20',
    ],
    DoorNameRules: [
        v => !!v || '',
        v => (v && v.length <= 10) || '10',
    ],
    ClientNameRules: [
        v => !!v || '',
        v => (v && v.length <= 10) || '10',
    ],
    ContactPersonRules: [
        v => !!v || '',
        v => (v && v.length <= 10) || '10',
    ],
    taxRules: [
        v => !!v || '',
        v => (v && v.length <= 20) || '20',
    ],
    selectRules: [
        v => !!v || '',
    ],
    addressRules: [
        v => !!v || ''
    ],
    effectiveDateRules: [
        v => !!v || '',
        v => (v && v.length <= 30) || '30',
    ],
    DoorSNRules: [
        v => !!v || '',
    ],
    emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ]
};